<template>
    <v-footer>
        <v-row
        align="center"
        justify="center"
       
      >
         <v-btn-toggle
          v-model="toggle"
          color = "#356859"
          group
          tile
          mandatory
          rounded
          v-on:change="changehandler"
        >
          <v-btn icon  >
              <v-icon>mdi-weather-sunny</v-icon>
          </v-btn>
          <v-btn icon >
              <v-icon>mdi-weather-night</v-icon>
          </v-btn>
        </v-btn-toggle>
        </v-row>
    </v-footer>
</template>

<script>
export default {
    data: () => ({
        toggle : 0
    }),
    methods : {
        changehandler : function (a) {
            this.$emit('changeTheme', a);
        }
    }

}
</script>